<template>
  <div class="bgs" v-bind:style="{ backgroundImage: 'url(bg/bglon2.webp),url(bg/bglon1.webp)' }">

    <b-row class="fh">
      <b-col cols="2">
        <LeftCom></LeftCom>
      </b-col>

      <b-col cols="8">

        <b-row class="bookview">
          <div class="book">
            <div id="pages" class="pages" v-if="kkk > 0">
              <div class="page" :class="getCl(0)" :style="getZindex(0)" @click="clicks(0)">
                <div class="book" v-bind:style="{ backgroundImage: 'url(bg/a.png), url(' + anh + ')' }"></div>
              </div>
              <div class="page2" :class="getCl(1)" :style="getZindex(1)" >
                <Modau></Modau>
              </div>
              <!-- noi dung -->
              <div class="page scolls" :class="getCl(2)" :style="getZindex(2)" @click="clicks(2)">
                <div class="box-content"> Có một anh chàng rất sợ vợ. Quanh năm anh ta không dám nói vợ nửa lời. Ngày nào
                  anh ta cũng phải làm theo lời sai khiến chỉ bảo của vợ. Ðã thế mà thôi đâu, nhiều lúc anh còn bị vợ đay
                  nghiến, nhiếc móc thậm tệ.<br>
                  <br>
                  Tức tối vô cùng, một hôm anh ra chợ tỉnh bói xem tại sao mình phải sợ vợ. Thầy bói không cần gieo quẻ,
                  liền nói:<br>
                  <br>
                  – Nhìn tướng mạo anh thì sợ vợ là phải, vì có chòm râu mọc quặp vào trong.<br>
                  <br>
                  Nghe thầy bói nói như vậy, hôm sau vợ đi vắng, ngổi ở nhà một mình, anh ta lấy gương soi rồi cầm râu
                  vuốt ra và nói:<br>
                  <br>
                  – Ngồi buồn thong thả, vuốt râu cho thẳng ra kẻo mà sợ vợ.<br>
                  <br>
                </div>
              </div>
              <div class="page scolls" :class="getCl(3)" :style="getZindex(3)" @click="clicks(3)">
                <div class="box-content"> Nào ngờ, vừa lúc đó, chị vợ đi làm về. Nghe được câu nói đó của chồng, chị gắt
                  giọng:<br>
                  <br>
                  – Ông ở nhà, nói cái gì lảm nhảm một mình như vậy?<br>
                  <br>
                  Anh ta giật mình lo sợ, liền vuốt râu vào mà nói:<br>
                  <br>
                  – Y cựu hoàn y cựu! Y cựu hoàn y cựu!<br>
                  <br>
                  Nguồn: Truyện cổ tích Tổng hợp.
                </div>
              </div>
              <!-- ket thuc phai chan-->
              <div class="page" :class="getCl(4)" :style="getZindex(4)" @click="denketthuc()">
                <KetThuc></KetThuc>
              </div>
            </div>
          </div>

        </b-row>

      </b-col>

      <b-col cols="2">
        <RightCom></RightCom>
      </b-col>

    </b-row>

  </div>
</template>
<script>

import KetThuc from '../components/KetThuc.vue'
import Modau from '../components/Modau.vue'
import { Clicks } from '../data/Click'
import LeftCom from '../components/Lefts.vue'
import RightCom from '../components/Right.vue'
export default {
  name: 'rau-quap',
  data: () => ({
    anh: 'https://cotich.net/uploads/news/a_2033506960_rau-quap.jpg',
    per: -1,
    next: -1,
    ht: -1,
    ht2: -1,
    arrClassScc: ["", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", ""],
    kkk: 1
  }),
  metaInfo: {
    title: 'Râu quặp - Truyện Cười',
    meta: [
      { name: 'thumbnail', content: 'https://cotich.net/uploads/news/a_2033506960_rau-quap.jpg' },
      { charset: 'utf-8' },
      { name: 'Keywords', content: 'Râu quặp' },
      { name: 'Description', content: 'Có một anh chàng rất sợ vợ. Quanh năm anh ta không dám nói vợ nửa lời. Ngày nào anh ta cũng phải làm theo lời sai khiến chỉ bảo của vợ. Ðã thế mà thôi đâu, nhiều lúc anh còn bị vợ đay nghiến, nhiếc móc thậm tệ. Tức tối vô cùng, một...' },

      { itemprop: 'name', content: 'Truyện Cười' },
      { name: 'viewport', content: 'width=device-width, initial-scale=1, maximum-scale=1' },
      { name: 'language', content: 'Vietnamese,vn' },
      { name: 'geo.region', content: 'VN' },
      { property: 'article:tag', content: 'Truyện Cười, Chuyện Cười' },
      { name: 'robots', content: 'index,follow' },
      { name: '', content: '' },
    ]
  },
  components: {
    LeftCom,
    RightCom,
    Modau,
    KetThuc
  },
  methods: {
    denmodau() {
      Clicks.clickmodau();
    },
    denketthuc() {
      Clicks.clickketthuc();
    },
    clicks(vts) {
      this.ht = vts + 1;
      this.ht2 = vts;
      this.per = vts - 1;
      this.next = vts + 1;
      if (this.ht % 2 === 0) {
        this.arrClassScc[this.ht2] = this.setClass(this.arrClassScc[this.ht2], false);
        if (this.per > 0) {
          this.arrClassScc[this.per] = this.setClass(this.arrClassScc[this.per], false);
        }
      } else {
        this.arrClassScc[this.ht2] = this.setClass(this.arrClassScc[this.ht2], true);
        if (this.next < this.arrClassScc.length) {
          this.arrClassScc[this.next] = this.setClass(this.arrClassScc[this.next], true);
        }
      }
      this.kkk++;
    },
    setClass(sClass, add) {
      var f = 'flipped';
      if (add) {
        if (sClass.indexOf(f) < 0) {
          sClass = sClass + " " + f;
        }
      } else {
        if (sClass.indexOf(" " + f) > 0) {
          sClass = sClass.replace(" " + f, "");
        } else
          if (sClass.indexOf(f) > 0) {
            sClass = sClass.replace(f, "");
          }
      }
      return sClass;
    },
    getCl(vt) {
      return this.arrClassScc[vt];

    },
    getZindex(vt) {
      if (vt % 2 === 0) {
        return "z-index: " + (4 - vt) + ";"
      } else {
        return "";
      }
    }
  },
  created() {
  }
}
</script>

<style>@import '../app.css';
@import 'truyen.css';</style>
